<template>
   	<va-modal v-model="showModal" :hide-default-actions="true" :mobile-fullscreen="false" title="teste">
		<div class="modal-chamados">
			<va-switch v-model="getObjeto().approved" label="Aprovar este chamado?" left-label color="verde" class="mb-3"/>
			<va-switch v-model="getObjeto().finished" label="Finalizar este chamado?" left-label color="verde" class="mb-3"/>
			<va-button @click="aprovarChamado()" color="verde" text-color="cinza"> Enviar</va-button>
		</div>
	</va-modal>
</template>

<script>
export default {
    name: 'ModalChamados',
	data: () => ({
        showModal: false,
		option: [false, true],
		objeto : {},
    }),
	computed: {
		getShow(){
			return this.$store.getters[`chamadosShow.getShow`];
		},
		getPermissao(){
			return this.$store.getters['membro.getPermissao']
		},
	},
	methods:{
		aprovarChamado(){
			this.objeto.approve({
				getters: this.$store.getters
			})
			this.$store.dispatch('toast.success', 'Atualizado');
			this.showModal= false;
		},
		getObjeto(){
			this.objeto = this.$store.getters[`chamadosShow.getObjeto`];
			return this.$store.getters[`chamadosShow.getObjeto`];
		},
	},
    watch: {
        getShow: function (value) {
            this.showModal = value
        },
		showModal: function (value) {
            this.$store.dispatch(`chamadosShow.change_show`, value);
        },
    }
}
</script>

<style scoped>

.modal-chamados{
	display: flex;
	flex-direction: column;
	min-width: 30vw;
	color: var(--branco);
}
</style>