<template>
	<FloatButton @click="refresh" icon="refresh" type="vuestic" />
	<FloatButton v-if="aba.model" @click="add" icon="add" type="vuestic" style="bottom: 90px" />

	<!-- {{getObjetos}} -->
	<TabelaGeral :aba="aba" :objetos="getObjetos ? getObjetos : []"/>
</template>

<script>
import FloatButton from "@/utils/FloatButton";
import TabelaGeral from '../utils/TabelaGeral'
export default {
	name: "SystemAdmin",
	components: {
		FloatButton, TabelaGeral
	},
	data: () => ({
		open: false,
		showAll: false
	}),
	computed: {
		getObjetos(){
			if (this.aba.tipo == "categorias") return this.$store.getters["categorias.get"].ativos;
			else return  this.$store.getters[this.aba.getters]
		},
	
		aba() {
			const index = this.$store.getters["abas.getIndices"][this.$route.path];
			const abas = this.$store.getters["abas.getAbas"][this.$route.path];
			return index != undefined && abas ? abas[index] : {};
		},
		id_empresa(){
			return this.$store.getters['empresa.get'].id
		},
		getFiltro(){
			const filtros = this.$store.getters['abas.getFiltro']
			return filtros && filtros[this.aba.tipo] ? filtros[this.aba.tipo] : {}
		}
	},
	watch: {
		aba() {
		this.loadData();
		},
	},
	methods: {
		add() {
			let permissao =  this.$store.getters['membro.getPermissao'];
			if(permissao < 100){
				this.$store.dispatch("edit-modal.editShow", {
					objeto: this.aba.model,
					callback: this.aba.callback,
				});
			}
			else{
				this.$store.dispatch('toast.error', 'No autorizado!')
			}
		},
		refresh() {
			this.loadData();
		},
		loadData() {
			const objeto = this.getFiltro
			if (this.aba && this.aba.callback) {
			if (!objeto) return this.$store.dispatch(this.aba.callback);
				let query = Object.keys(objeto).reduce(function (previous, key) {
					if (objeto[key])
						return `${previous}&${key}=${objeto[key]}` ;
					else
						return previous;
				}, "").replace('&', '')
				this.$store.dispatch(`${this.aba.tipo}.load`, query)  
			}
		},
	},
	mounted() {
		this.loadData();
		this.$store.dispatch("ativos.loadCategorias");
		this.$store.dispatch("categorias.load", "ativos");
		this.$store.dispatch("admin.load");
	},
};
</script>

<style scoped>
.system-admin {
  margin: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  /*display: grid;*/
  /*grid-template-rows: 150px 1fr;*/
  /*grid-template-areas:
    "estatistica"
    "patrimonio-tabela";*/
}

</style>
