import { url, model } from '@/configs/global'

class Usuario extends model{
    constructor(data, axios) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'usuario'
        this.listaCriacao = [
            {label: 'name', type: 'text', labelInput: 'Usurio'},
            {label: 'email', type: 'text', labelInput: 'EMAIL'}
        ]
        this.listaAtualizacao = [
            ...this.listaCriacao,
            {label: 'level_permission', type: 'text', labelInput: 'Nvel de permisso'},
            {label: 'level_permission', type: 'select',  textBy: 'label', valueBy: 'id', trackBy: 'id', labelInput: 'Nvel de Permisso',
            options: () => [{'id': 0, 'label': 'Administrador Master'}, {'id': 1, 'label': 'Administrador Secundrio'}, {'id': 100, 'label': 'Usurio comum'}]},
            {label: 'status', type: 'select',  textBy: 'label', valueBy: 'id', trackBy: 'id', labelInput: 'Status',
            options: () => [{'id': 1, 'label': 'Ativo'}, {'id': 0, 'label': 'Inativo'}]},
        ]
    }

    async create(){
        return await this.axios.post(`${url}/register`, {...this})
    }

    async update(){
        return await this.axios.post(`${url}/api/update_user/${this.id}`, {...this})
    }
    
    static async getLoggedUser(axios){
        const res = await axios.get(`${url}/api/get_logged_user`)
        return res.data
    }

    static async getAllCompanies(axios){
        const res = await axios.get(`${url}/api/get_companies`)
        return res.data.data
    }

    static async reset_password(axios, obj){
        const res = await axios.post(`${url}/api/reset_password`, obj)
        return res.data.message
    }
}

export default Usuario